<template>
  <div id="dashboard">
    <ul class="collection with-header">
      <li class="collection-header">
        <h4>Events</h4>
      </li>
      <li v-for="event in events" v-bind:key="event.id" class="collection-item">
        <div class="chip">
          ID: {{event.event_id}} |
          Category: {{event.category}} |
          Name: {{event.name}}
        </div>
        <router-link v-bind:to="{name: 'view-event', params:{event_id:event.event_id}}" class="secondary-content">
          <i class="fa fa-eye"></i>
        </router-link>
      </li>
    </ul>
    <div class="fixed-action-btn">
      <router-link to="/new" class="btn-floating btn-large red pulse">
        <i class="fa fa-plus"></i>
      </router-link>
    </div>
  </div>
</template>

<script>
import db from './firebaseInit'
  export default {
    name: 'dashboard',
    data() {
      return {
        events: []
      }
    },
    created(){
      db.collection('event').orderBy('event_id').get().then(
         querySnapshot => {
        querySnapshot.forEach(doc => {
          const data = {
            'id': doc.id,
            'event_id': doc.data().event_id,
            'name': doc.data().name,
            'category': doc.data().category,
            'description': doc.data().description
          }
          this.events.push(data)
        })
      })
    }
  }
</script>



