<template>
  <div id="new-event">
    <h3>Add New Event</h3>
    <div class="row">
      <form @submit.prevent="saveEvent" class="col s12">
        <div class="row">
          <div class="inptu-field col s12">
            <label>Event ID#</label>
            <input type="text" v-model="event_id" required placeholder="00000">
          </div>
        </div>
        <div class="row">
          <div class="inptu-field col s12">
            <label>Name</label>
            <input type="text" v-model="name" required placeholder="Cafe Charol">
          </div>
        </div>
        <div class="row">
          <div class="inptu-field col s12">
            <label>Category</label>
            <input type="text" v-model="category" required placeholder="Resturant">
          </div>
        </div>
        <div class="row">
          <div class="inptu-field col s12">
            <label>Description</label>
            <input type="text" v-model="description" required placeholder="Description of the event">
          </div>
        </div>
        <button type="submit" class="btn">Submit</button>
        <router-link to="/" class="btn red">Cancel</router-link>
      </form>
    </div>
  </div>
</template>

<script>
import db from './firebaseInit'
  export default {
    name: 'new-event',
    data() {
      return {
        event_id: null,
        name: null,
        category: null,
        description: null
      }
    },
    methods: {
      saveEvent (){
        db.collection('event').add({
          event_id: this.event_id,
          name: this.name,
          category: this.category,
          description: this.description
        }).then(docRef => {
          this.$router.push('/')
          Materialize.toast('New Event Added!', 1000);
        })
      }
    }
  }
</script>



