<template>
  <div id="edit-event">
    <h3>Edit Event</h3>
    <div class="row">
      <form @submit.prevent="updateEvent" class="col s12">
        <div class="row">
          <div class="inptu-field col s12">
            <label>Event ID#</label>
            <input disabled type="text" v-model="event_id" required placeholder="00000">
          </div>
        </div>
        <div class="row">
          <div class="inptu-field col s12">
            <label>Name</label>
            <input type="text" v-model="name" required placeholder="Cafe Charol">
          </div>
        </div>
        <div class="row">
          <div class="inptu-field col s12">
            <label>Category</label>
            <input type="text" v-model="category" required placeholder="Resturant">
          </div>
        </div>
        <div class="row">
          <div class="inptu-field col s12">
            <label>Description</label>
            <input type="text" v-model="description" required placeholder="Description of the event">
          </div>
        </div>
        <button type="submit" class="btn">Submit</button>
        <router-link to="/" class="btn red">Cancel</router-link>
      </form>
    </div>
  </div>
</template>

<script>
import db from './firebaseInit'
  export default {
    name: 'edit-event',
    data() {
      return {
        event_id: null,
        name: null,
        category: null,
        description: null
      }
    },beforeRouteEnter(to,from,next){
      db.collection('event').where('event_id','==', to.params.event_id).get().then(querySnapshot => {
        querySnapshot.forEach(doc => {
          next(vm => {
            vm.event_id = doc.data().event_id
            vm.name = doc.data().name
            vm.category = doc.data().category
            vm.description = doc.data().description
          })
        })
      })
    },
    watch: {
      '$route': 'fetchData'
    },
    methods: {
      fetchData (){
        db.collection('event').where('event_id', '==', this.$route.params.event_id).get().then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.event_id = doc.data().event_id
            this.name = doc.data().name
            this.category = doc.data().category
            this.description = doc.data().description
          })
        })
      },
      updateEvent (){
        db.collection('event').where('event_id', '==', this.$route.params.event_id).get().then(querySnapshot => {
          querySnapshot.forEach(doc => {
            doc.ref.update({
              event_id: this.event_id,
              name: this.name,
              category: this.category,
              description: this.description
            }).then(() => {
              this.$router.push({name: 'view-event', params: {event_id: this.event_id}})
              Materialize.toast('Event Edited!', 1000);
            })
          })
        })
      }
    }
  }
</script>



